<template>

  <div class="box">

    <div class="box-header with-border">
      <h3 class="box-title">role Example</h3>
      <div class="box-tools pull-right">
        <!-- Buttons, labels, and many other things can be placed here! -->
        <!-- Here is a label for example -->
        <span class="label label-primary">Label</span>
      </div>
    </div>

    <div class="box-body table-responsive">
      <table id="roleManageId" class="table table-hover table-bordered"></table>
    </div>

    <div class="box-footer">
      The footer of the box
      <!--<pagination-->
    </div>

  </div>

</template>

<script>
import {roleManage} from '../../assets/myjs/AuthManage'
export default {
  data () {
    return {
      routerName: ''
    }
  },
  mounted: function () {
    this.routerName = this.$router.currentRoute.name
    roleManage.initRoleTable()
  },
  methods: {
    // 必须禁掉form默认的submit
    disableFormSubmit: function () {
      return false
    }

  }
  // components: {pagination}
}
</script>
<style>

</style>



