import {getRequest} from './rest'
import {Message} from 'element-ui'

export const isNotNullORBlank = (...args) => {
  for (var i = 0; i < args.length; i++) {
    var argument = args[i]
    if (argument == null || argument === '' || argument === undefined) {
      Message.warning({message: '数据不能为空!'})
      return false
    }
  }
  return true
}

/*
* 将时间戳装换成yyyy-MM-dd HH-mm-ss的日期格式
*/
export const timeStampToDate = (_timestamp) => {
  if (_timestamp != null && _timestamp !== '') {
    var _date = new Date(_timestamp); var year = _date.getFullYear()
    var month = _date.getMonth() + 1; month = append0(month)
    var day = _date.getDate(); day = append0(day)
    var hour = _date.getHours(); hour = append0(hour)
    var minute = _date.getMinutes(); minute = append0(minute)
    var second = _date.getSeconds(); second = append0(second)
    return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
  }
}
var append0 = function (_obj) {
  return _obj < 10 ? '0' + _obj : _obj
}

export const initMenu = (router, store) => {
  if (store.state.routes.length > 0) return
  // TODO addRouter()，刷新了页面后 vuex和router里面的信息都被重新初始化了，此时要重新设置动态路由，  所以判断vuex中新增的数据，如果为空则用户刷新了页面
  // var menus = sessionStorage.getItem('menus')
  // console.log(JSON.parse(menus))
  // if (menus) {
  //   console.log(111)
  //   initRouterConfig(router, store, JSON.parse(menus))
  //   return
  // }
  getRequest('/admin/menus').then(resp => {
    console.log('req -->> /config/menus -------------------------')
    if (resp && resp.status === 200) {
      // 保存菜单
      // sessionStorage.setItem('menus', JSON.stringify(resp.data))
      initRouterConfig(router, store, resp.data)
    }
  })
}

// 动态添加路由
var initRouterConfig = function (router, store, menus) {
  var fmtRoutes = formatRoutes(menus)
  fmtRoutes.forEach(fr => {
    var childrenMenu = fr.children
    childrenMenu.forEach(chfr => {
      router.options.routes[0].children.push(chfr)
    })
  })
  router.addRoutes(router.options.routes) // 调用addRoutes添加路由
  store.commit('initMenu', fmtRoutes)
}

// https://router.vuejs.org/zh/api/#router-构建选项
export const formatRoutes = (routes) => {
  let fmRoutes = []
  routes.forEach(router => {
    let {
      path,
      modular,
      component,
      name,
      meta,
      icon,
      children
    } = router
    if (children && children instanceof Array) {
      children = formatRoutes(children)
    }
    let fmRouter = {
      path: path,
      component: (resolve) => {
        if (modular === 'admin') {
          require(['../components/admin/' + component + '.vue'], resolve)
        }
      },
      name: name,
      icon: icon,
      meta: meta,
      children: children
    }
    fmRoutes.push(fmRouter)
  })
  return fmRoutes
}



