import {timeStampToDate} from '../../utils/utils'
import { Message } from 'element-ui'
import {ftmURL} from '../../utils/rest'
// import BootstrapDialog from 'bootstrap3-dialog/dist/js/bootstrap-dialog'

/**
 * 用户管理
 */
const userManage = {
  initUserTable () {
    $('#userManageId').bootstrapTable('destroy').bootstrapTable({
      method: 'post',
      // url: '/admin/userTable',
      url: ftmURL('/admin/userTable'),
      // height: "450",
      striped: false, // 不显示斑马线
      clickToSelect: true, // 点击行即可选中单选/复选框
      dataType: 'json',
      contentType: 'application/x-www-form-urlencoded',
      pagination: true, // 启动分页
      pageSize: 5, // 每页显示的记录数
      pageNumber: 1, // 当前第几页
      pageList: [5, 10, 20, 50, 100, 500, 5000, 10000], // 记录数可选列表
      search: false, // 是否启用查询,是客户端client才有效
      searchOnEnterKey: true, // 按回车触发搜索方法，否则自动触发搜索方法
      showColumns: true, // 显示下拉框勾选要显示的列
      showExport: true, // 是否显示导出
      exportDataType: 'basic',
      showRefresh: true, // 显示刷新按钮
      silent: true, // 刷新事件必须设置
      strictSearch: true, // 全匹配搜索，否则为模糊搜索
      showToggle: false, // 显示 切换试图（table/card）按钮
      // toolbar: '#biddingBlockTool',
      singleSelect: false, // 多选
      exportTypes: ['csv', 'txt', 'sql', 'doc', 'excel', 'xlsx'], // 导出文件类型
      // uniqueId: "id", //每一行的唯一标识，一般为主键列
      // editable: false,
      sidePagination: 'server', // 服务器端请求

      rowStyle: function (row, index) {
        return row.enable ? {} : {classes: 'warning'}
      },
      columns: [
        {field: 'state', checkbox: true, width: 30},
        {field: 'id', title: 'id', visible: false},
        {
          field: 'username',
          title: '用户名',
          formatter: function (value, row, index) {
            return '<strong><a>' + value + '</a></strong>'
          }
        },
        {
          field: 'enable',
          title: '是否可用',
          formatter: function (value, row, index) {
            return value ? '可用' : '不可用'
          }
        },
        {field: 'name', title: '名称'},
        {field: 'phone', title: '电话'},
        {field: 'email', title: '邮箱'},
        {field: 'userface', title: '头像'},
        {
          field: 'createTime',
          title: '创建日期',
          sortable: true,
          formatter: function (value, row, index) {
            if (value != null && value !== '') {
              return timeStampToDate(value)
            }
          }
        },
        {
          field: 'updateTime',
          title: '更新日期',
          sortable: true,
          formatter: function (value, row, index) {
            if (value != null && value !== '') {
              return timeStampToDate(value)
            }
          }
        }
      ],

      // 设置为undefined可以获取pageNumber，pageSize，searchText，sortName，sortOrder
      // 设置为limit可以获取limit, offset, search, sort, order
      queryParamsType: 'undefined',
      queryParams: function queryParams (params) {
        var param = {
          pageNumber: params.pageNumber,
          pageSize: params.pageSize,
          sortName: params.sortName,
          sortOrder: params.sortOrder
        }
        return param
      },
      onLoadSuccess: function (data) { // 加载成功时执行
      },
      onLoadError: function (status) { // 加载失败时执行
        switch (status) {
          case 0: break // 刷新太快(canceled)
          case 401: Message.error({message: '您的回话已过期, 请重新登录!', showClose: true}); break
          default: Message.error({message: '数据加载失败! ' + status, showClose: true})
        }
      },

      // 点击
      onClickCell: function (field, value, row, $element) {
        if (field === 'username') {
          userManage.queryRoles(row.id)
          userManage.queryMenus(row.id)
        }
      },

      // 选中
      onCheck: function (row, tr, field) {
        console.log('选中... user')
      },
      onUncheck: function (row, tr) {
        console.log('取消... user')
      },

      // table全部选中时
      onCheckAll: function (rows) {
        console.log('全部选中... user')
      },
      onUncheckAll: function (rows) {
        console.log('全部取消... user')
      }
    })
  },
  // 根据用户id 查询该用户的角色
  queryRoles (id) {
    console.log('query roles.. by uid, id=' + id)
  },
  // 根据用户id 查询该用户的菜单
  queryMenus (id) {
    console.log('query menus@@ by uid, id=' + id)
  }
}

