<template>
  <div class="container" style="background-image: url('../assets/images/login_backgroud03.jpg')">
    <!--<img src="../assets/images/logo.png" class="img-responsive center-block animated flipInX" style="margin-top: 8%; width: 42%">-->

    <div class="text-center">
      <div>
        <h3 class="animated pulse">Welcome to go iris web server !</h3>
        <div class="row">
          <div class="col-xs-12 col-md-5" style="margin: auto; padding: auto; display: block; float: none">

            <form role="form" id="login_form" class="animated rollIn"
                  v-on:submit.prevent="disableFormSubmit">
              <p>&nbsp;</p>

              <div class="form-group" :class="{'has-error': errors.has('用户名')}">
                <input type="text" class="form-control" placeholder="用户名" required
                       data-vv-name="用户名"
                       v-model="loginForm.username"
                       v-validate="'required'">
                <div class="pull-left" :class="{'text-danger': errors.has('用户名')}">
                  <i v-show="errors.has('用户名')" class="fa fa-warning"></i>
                  <span v-show="errors.has('用户名')"> {{ errors.first('用户名') }} </span>
                </div>
              </div>

              <div class="form-group" :class="{'has-error': errors.has('密码')}">
                <input type="password" class="form-control" placeholder="密码" required
                       data-vv-name="密码"
                       v-model="loginForm.password"
                       v-validate="'required|min:4'">
                <div class="pull-left" :class="{'text-danger': errors.has('密码')}">
                  <i v-show="errors.has('密码')" class="fa fa-warning"></i>
                  <span v-show="errors.has('密码')" class="info" v-cloak> {{ errors.first('密码') }} </span>
                </div>
              </div>

              <button @click="submit" class="btn btn-sm btn-primary btn-block">登 录</button>
              <p class="text-muted text-center">
                <a href="#">
                  <small>忘记密码了？</small>
                </a> | <a href="#">注册一个新账号</a>
              </p>
            </form>

          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      }
    }
  },
  mounted: function () {
  },
  methods: {
    // 必须禁掉form默认的submit
    disableFormSubmit: function () {
      return false
    },

    submit () {
      this.$validator.validate().then(valid => {
        console.log(valid)
        if (valid) { // 验证通过
          this.postRequest('/user/login', this.loginForm)
            .then(resp => {
              if (resp && resp.status === 200) {
                var data = resp.data
                this.$store.commit('login', data)
                var path = this.$route.query.redirect
                // this.$router.replace({path: path == '/' || path == undefined ? '/home' : path});
                this.$router.push({path: (path === '/' || path === undefined ? '/home' : path)})
              }
            })
        }
      })
    }

  }
}
</script>
<style>
  #login_form {
    background: rgba(90%, 90%, 90%, 0.3);
    border-radius: 3px;
  }
</style>



