<template>

  <div class="box">

    <div class="box-header with-border">
      <h3 class="box-title">{{routerName}}</h3>
      <div class="box-tools pull-right">
        <!-- Buttons, labels, and many other things can be placed here! -->
        <!-- Here is a label for example -->
        <!--<span class="label label-primary">Label</span>-->
      </div>
    </div>

    <div class="box-body table-responsive">
      <div class="row">
        <div id="menuTool" class="col-xs-12" style="color: red;font-size: 12px;">
          <a class="btn btn-success btn-xs" data-target="#menuModal" data-toggle="modal">
            <i class="fa fa-plus" aria-hidden="true"></i> 新增
          </a>
          <a @click="deleteMenus" class="btn btn-danger btn-xs"><i class="fa fa-times" aria-hidden="true"></i> 删除</a>
          <a @click="clickUpdate" class="btn btn-info btn-xs"><i class="fa fa-pencil-square-o" aria-hidden="true"></i> 修改</a>
        </div>
      </div>
      <table id="menuManageId" class="table table-hover table-bordered"></table>
    </div>

    <div class="box-footer">
      The footer of the box
      <!--<pagination-->
    </div>

    <!-- 菜单的模态框 -->
    <div class="modal fade" id="menuModal" tabindex="-1">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true" id="x">&times;</span><span
              class="sr-only">Close</span></button>
            <h3 class="modal-title">{{isAdd ? '添加菜单' : '修改菜单'}}</h3>
          </div>

          <div class="modal-body">
            <form action="" class="form-horizontal" role="form" method="post">
              <div class="form-group" :class="{'has-error': errors.has('菜单名称')}">
                <label class="col-sm-3 control-label">菜单名称:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="text" class="form-control" required="" aria-required="true"
                         data-vv-name="菜单名称"
                         v-model="oneMenu.name"
                         v-validate="'required'">
                  <div class="pull-left" :class="{'text-danger': errors.has('菜单名称')}">
                    <i v-show="errors.has('菜单名称')" class="fa fa-warning"></i>
                    <span v-show="errors.has('菜单名称')"> {{ errors.first('菜单名称') }} </span>
                  </div>
                </div>
              </div>

              <div class="form-group" :class="{'has-error': errors.has('path')}">
                <label class="col-sm-3 control-label">path:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="text" class="form-control" required="" aria-required="true"
                         data-vv-name="path"
                         v-model="oneMenu.path"
                         v-validate="'required'">
                  <div class="pull-left" :class="{'text-danger': errors.has('path')}">
                    <i v-show="errors.has('path')" class="fa fa-warning"></i>
                    <span v-show="errors.has('path')"> {{ errors.first('path') }} </span>
                  </div>
                </div>
              </div>

              <div class="form-group">
                <label class="col-sm-3 control-label">url:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="text" class="form-control" required="" aria-required="true"
                         v-model="oneMenu.url">
                </div>
              </div>

              <div class="form-group" :class="{'has-error': errors.has('模块')}">
                <label class="col-sm-3 control-label">模块:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="text" class="form-control" required="" aria-required="true"
                         data-vv-name="模块"
                         v-model="oneMenu.modular"
                         v-validate="'required'">
                  <div class="pull-left" :class="{'text-danger': errors.has('模块')}">
                    <i v-show="errors.has('模块')" class="fa fa-warning"></i>
                    <span v-show="errors.has('模块')"> {{ errors.first('模块') }} </span>
                  </div>
                </div>
              </div>

              <div class="form-group" :class="{'has-error': errors.has('组件')}">
                <label class="col-sm-3 control-label">组件:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="text" class="form-control" required="" aria-required="true"
                         data-vv-name="组件"
                         v-model="oneMenu.component"
                         v-validate="'required'">
                  <div class="pull-left" :class="{'text-danger': errors.has('组件')}">
                    <i v-show="errors.has('组件')" class="fa fa-warning"></i>
                    <span v-show="errors.has('组件')"> {{ errors.first('组件') }} </span>
                  </div>
                </div>
              </div>

              <div class="form-group">
                <label class="col-sm-3 control-label">图标:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="text" class="form-control" required="" aria-required="true"
                         v-model="oneMenu.icon">
                </div>
              </div>

              <div class="form-group">
                <label class="col-sm-3 control-label">父节点:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="number" class="form-control" required="" aria-required="true"
                         v-model="oneMenu.parentId">
                </div>
              </div>

              <div class="form-group">
                <label class="col-sm-3 control-label">是否可用:</label>
                <div class="col-sm-7">
                  <input minlength="1" type="number" class="form-control" required="" aria-required="true"
                         v-model="oneMenu.enabled">
                </div>
              </div>
            </form>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-default btn-xs" data-dismiss="modal">close</button>
            <button v-if="isAdd" type="button" class="btn btn-primary btn-xs" @click="createOneMenu">添加</button>
            <button v-else type="button" class="btn btn-primary btn-xs" @click="updateOneMenu">修改</button>
          </div>
        </div>
      </div>
    </div>

  </div>

</template>

<script>
import {menuManage} from '../../assets/myjs/AuthManage'
import {timeStampToDate} from '../../utils/utils'
export default {
  data () {
    return {
      routerName: '',
      isAdd: true, // 默认是添加菜单类型
      oneMenu: {}
    }
  },
  mounted () {
    this.routerName = this.$router.currentRoute.name
    menuManage.initMenuTable()

    let m = this
    this.$nextTick(function () {
      $('#menuModal').on('hidden.bs.modal', function () {
        m.isAdd = true
        m.oneMenu = {}
        m.$validator.reset()
      })
    })
  },
  methods: {
    disableFormSubmit () {
      return false
    },
    // new one menu
    createOneMenu () {
      this.$validator.validate().then(valid => {
        console.log(valid)
        if (valid) { // 验证通过
          this.postRequest('/admin/menu', this.oneMenu)
            .then(resp => {
              if (resp && resp.status === 200) {
                var data = resp.data
                this.$message.success({message: '成功创建了 ' + data.data + ' 个菜单', showClose: true})
                if (data.data === 1) {
                  this.oneMenu.createTime = timeStampToDate(new Date().getTime())
                  $('#menuManageId').bootstrapTable('append', this.oneMenu)
                }
                $('#menuModal').modal('toggle')
              }
            })
        }
      })
    },
    // del menus
    deleteMenus () {
      let $MenuTable = $('#menuManageId')
      var idList = $.map($MenuTable.bootstrapTable('getSelections'), function (row) {
        return row.id
      })

      let len = idList.length
      if (len > 0) {
        this.$confirm('此操作将永久删除 ' + len + ' 个菜单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let ids = ''
          for (var i in idList) {
            ids += (idList[i] + '-')
          }
          this.deleteRequest('/admin/' + ids + '/menu')
            .then(resp => {
              if (resp.data.data > 0) {
                this.$message.success({message: '删除成功!', showClose: true})
                $MenuTable.bootstrapTable('remove', {field: 'id', values: idList})
              }
            })
        }).catch(() => {
          this.$message.info({message: '已取消删除', showClose: true})
        })
      } else {
        this.$message.error({message: '请选择要删除的菜单!', showClose: true})
      }
    },
    // only click one menu
    clickUpdate () {
      var menus = $.map($('#menuManageId').bootstrapTable('getSelections'), function (row) {
        return row
      })
      let len = menus.length
      switch (len) {
        case 0: this.$message.error({message: '请选择要修改的菜单!', showClose: true}); break
        case 1: {
          this.isAdd = false
          $('#menuModal').modal('toggle')
          for (let i in menus[0]) {
            let val = menus[0][i]
            if (val !== undefined && val !== null) { this.oneMenu[i] = val }
          }
          break
        }
        default: this.$message.info({message: '只能修改一个菜单!', showClose: true})
      }
    },
    // only update one menu
    updateOneMenu () {
      $('#menuModal').modal('toggle')
      let id = this.oneMenu.id
      if (id !== undefined) {
        this.putRequest('/admin/menu', this.oneMenu)
          .then(resp => {
            if (resp.data.data > 0) {
              this.$message.success({message: '更新成功!', showClose: true})
              // $('#menuManageId').bootstrapTable('updateByUniqueId', {id: id, row: amenu}
              menuManage.initMenuTable()
            }
          })
      }
    }
  }
}

</script>
<style>

</style>



